export default {
	data() {
		return {
			categoryList: []
		}
	},
	mounted() {
		uni.setNavigationBarTitle({
			title: "分类"
		})
		this.getPageListData()
	},
	methods: {
		//获取列表数据
		getPageListData() {
			this.request({
				url: "http://157.122.54.189:9088/image/v1/vertical/category"
			}).then(result => {
				this.categoryList = result.res.category
			})
		},
		//点击事件
		handleItemClick(id) {
			uni.navigateTo({
				url: "/pages/category-detail/category-detail?id=" + id
			})
		}
	}
}
